.box1 {
  transform-origin: center;
  background-color: #8a2be2;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  transition: all .5s linear;
  position: absolute;
  top: 0;
  left: 35%;
}

.box2 {
  background-color: brown;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  transition: all .5s linear;
  position: absolute;
  left: 45%;
}

.game {
  text-align: center;
  width: 650px;
  margin-top: 570px;
  margin-left: auto;
  margin-right: auto;
  font-family: Courier New, Courier, monospace;
  font-weight: bold;
  position: relative;
}

.game > p {
  font-size: 30px;
  position: relative;
}

.start-button {
  cursor: pointer;
  background-color: #5f9ea0;
  border: none;
  border-radius: 40px;
  margin-bottom: 20px;
  padding: 20px 30px;
  font-size: 20px;
  transition: all .25s linear;
}

.start-button:hover, .start-button:focus {
  border-radius: 20px;
  scale: 1.1;
}

.difficulty-form {
  margin-bottom: 30px;
}

.difficulty-label {
  font-size: 20px;
}

.easy {
  color: green;
}

.medium {
  color: #ff0;
}

.difficult {
  color: red;
}

.buttons-container-1 {
  grid-template-columns: repeat(2, 1fr);
  row-gap: 30px;
  display: grid;
}

.buttons-container-1 > .btn-cont {
  text-align: center;
  justify-content: center;
  display: flex;
}

.buttons-container-2, .buttons-container-3 {
  opacity: 0;
  cursor: auto;
  transition: all .25s linear;
  position: absolute;
}

.buttons-container-2 > div:not(:last-of-type), .buttons-container-3 > div:not(:last-of-type) {
  margin-bottom: 20px;
}

.buttons-container-2 {
  top: -10px;
  left: -80px;
}

.buttons-container-3 {
  top: -10px;
  right: -80px;
}

.buttons-container-2.active, .buttons-container-3.active {
  opacity: 1;
  transition: all .25s linear;
}

.game-button {
  color: #fff;
  cursor: pointer;
  background-color: gray;
  border: none;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  padding: 0;
  font-size: 30px;
  transition: all .2s linear;
}

.game-button:hover {
  scale: 1.1;
}

.game-button:disabled {
  cursor: auto;
}

.game-button:disabled:hover {
  scale: 1;
}

.timer {
  opacity: .5;
  background-color: green;
  border-radius: 10px;
  height: 15px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: none;
  position: absolute;
  bottom: 90%;
  left: 50%;
  transform: translate(-50%);
}

.timer.active {
  display: block;
}

.stopwatch {
  text-align: center;
  justify-content: center;
  margin-top: 200px;
  margin-bottom: 100px;
  display: flex;
}

.stopwatch-button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  padding: 0;
  position: relative;
  box-shadow: 6px 4px 8px gray;
}

.stopwatch-button:after {
  content: "";
  background-image: url("stopwatch.2838987e.svg");
  background-repeat: no-repeat;
  width: 80%;
  height: 80%;
  display: inline-flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch-input {
  box-sizing: border-box;
  border: none;
  width: 250px;
  height: 100px;
  margin-right: 40px;
  padding: 0 0 0 20px;
  font-size: 40px;
  box-shadow: 4px 4px 8px gray;
}

.stopwatch-button > svg {
  width: 100%;
  height: 100%;
}

